export const logInTemplate = (isLogin): string =>
  isLogin
    ? `<div class="permission-info">  .</div>`
    : `
    <h1 class="title"></h1>
    <form id="login-form">
      <label></label>
      <input id="login-email-input" class="login-input" placeholder="woowacourse@gmail.com" type="email" required/>
      <label></label>
      <input id="login-password-input" class="login-input" placeholder=" " type="password" required/>
      <button></button>
    </form>
    <div class="login-to-signup">  ? <span id="go-to-signup"></span></div>
    `;
