export const changeUserInfoTemplate = (isLogin, user): string =>
  isLogin
    ? `
    <h1 class="title">  </h1>
    <form id="change-form">
      <label></label>
      <input id="change-email-input" required value=${user.email} disabled/>
      <label></label>
      <input id="change-name-input" placeholder=" " required/>
      <label></label>
      <input id="change-password-input" placeholder=" " type="password" required/>
      <label> </label>
      <input id="change-password-check-input" placeholder=" " type="password" required/>  
      <button></button>
    </form>`
    : `<div class="permission-info">  .</div>`;
